* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}
nav {
  height: 100px;
  display: flex;
  padding: 20px 36px;
  box-shadow: 20px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
  /* border-bottom: 1px solid red; */
  
}
.navbar{
  display: flex;
  margin-left: 10px;
  align-items: center;
  justify-content: center;
  color:lightcoral;
}
.bold {
  font-weight: bold;
  
}

.gray {
  color: #918E9B;
}
.nav--logo {
  max-width: 100px;
}

section {
  padding: 20px;
}

.hero {
  display: flex;
  flex-direction: column;
}

.hero--photo {
  max-width: 400px;
  align-self: center;
}
.hero--header {
  margin-bottom: 16px;
}

.hero--text {
  margin-top: 0;
}
.cards-list {
  display: flex;
  flex-wrap: nowrap;
  gap: 20px;
  overflow-x: auto;
}
.card {
  width: 175px;
  font-size: 12px;
  flex: 0 0 auto;         /* ADD */
  display: flex;          /* ADD */
  flex-direction: column;  /*ADD */
  position: relative;
  
}

.card--image {
  width: 100%;
  height: 145px;
  border-radius: 9px;
  margin-bottom: 9px;
  
}
/* ADD */
.card--title {
  overflow: hidden;
  text-overflow: ellipsis;
}
.card--stats {
  display: flex;
  align-items: center;
}

.card--star {
  height: 14px;
 
}/* ADD */
.card--price {
  margin-top: auto;
  
}.card--badge{
  position: absolute;
  top: 6px;
  left: 6px;
  background-color: white;
  padding: 5px 7px;
  border-radius: 2px;
  font-weight: bold;
}